// Reset the box model measurements.
@mixin shila-reset-box-model {
  margin: 0;
  padding: 0;
  border: 0;
}

@mixin shila-outer-container {
  max-width: $shila-max-width;
  margin-left: auto;
  margin-right: auto;
}

@mixin shila-side-margins {
  margin-left: $shila-base-side-spacing;
  margin-right: $shila-base-side-spacing;
  @each $breakpoint-key, $side-spacing in $shila-breakpoint-side-spacings {
    @include breakpoint(shila-breakpoint($breakpoint-key)) {
      margin-left: $side-spacing;
      margin-right: $side-spacing;
    }
  }
}
